/* make box-sizing predictable to avoid padding causing overflow */
*, *::before, *::after {
  box-sizing: border-box;
}

/* Importar fuente "robótica" */
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700&display=swap');

.App {
  text-align: center;
  background-color: #161616;
  /* font-family moved to index.css */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #141e33; /* header principal no cambia */
  min-height: 7vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.area {
  min-height: 93vh;
  display: grid;
  gap: 1rem;
  /* Vertical (portrait) default: two rows */
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
}

/* Switch to two columns when device is in landscape */
@media (orientation: landscape) {
  .area {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: none;
  }
}

/* Optional: visual / sizing helpers */
.panel {
  padding: 1rem;
  background: #080613;
  
  min-height: 120px; /* optional */
  color: white;
  position: relative;
  overflow-x: hidden; /* evita scroll lateral */
  overflow-y: auto;   /* mantiene scroll vertical si hace falta */
}

/* Header fijo dentro del panel */
.list-header {
  position: sticky;
  top: 0;
  display: flex;
  gap: 0.5rem;
  align-items: center;
  justify-content: space-between;
  
  color: #071029; /* texto oscuro como en el botón */
  padding: 0.5rem 0.75rem;
  z-index: 20;
  border-bottom: 1px solid rgba(7,16,41,0.08);
  backdrop-filter: blur(4px);

  /* fuerza que ocupe toda la fila en el contenedor flex */
  flex: 0 0 100%;
}

/* Asegurar que el botón siempre esté encima */
.mode-toggle {
  z-index: 30;
  margin-left: 0.5rem;
  flex: 0 0 auto;
  width: 46px;
  height: 26px;
  padding: 3px;
  border-radius: 999px;
  border: none;
  background: rgba(255,255,255,0.06); /* estado OFF */
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  transition: none;
  position: relative; /* para posicionar el knob */
}

/* cuando está activo usar degradado (coherente con botones) */
.mode-toggle[aria-pressed="true"] {
  background: linear-gradient(90deg, #2b7cff 0%, #61dafb 100%);
  box-shadow: 0 6px 18px rgba(97,218,251,0.12);
}

/* hide/remove knob visuals if still present */
.toggle-knob {
  display: none;
}

/* no animations, only color change */
.mode-toggle {
  transition: none;
  box-shadow: none;
}

/* color del toggle según modo (sin animación) */
.person-mode .mode-toggle {
  background: linear-gradient(90deg, #2b7cff 0%, #61dafb 100%);
}

.expense-mode .mode-toggle {
  background: linear-gradient(90deg, #ff8a00 0%, #ffb347 100%);
}

/* asegurar foco accesible (sin animación) */
.mode-toggle:focus-visible {
  outline: none;
  box-shadow: 0 0 0 4px rgba(97,218,251,0.12);
}

.list-controller {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  justify-content: center;
  padding: 0.75rem;
  flex-wrap: wrap;
  min-width: 0;
}

.label {
  color: #ffffff;              /* título blanco */
  font-size: 1.12rem;          /* un poco más grande */
  font-weight: 700;
  font-family: 'Orbitron', sans-serif; /* asegurar Orbitron */
  margin-right: 0.25rem;
  text-align: left; /* asegurar alineacion izquierda dentro del header */
  flex: 1 1 auto;
}

.fancy-input {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.03), rgba(255, 255, 255, 0.01));
  border: 1px solid rgba(255, 255, 255, 0.06);
  padding: 0.55rem 0.75rem;
  margin: 3px;
  border-radius: 10px;
  color: #fff;
  min-width: 0;   /* permitir que se reduzca dentro de flexbox */
  max-width: 100%; /* no exceder el ancho disponible */
  box-shadow: 0 4px 10px rgba(3, 8, 20, 0.45);
  transition: box-shadow 120ms ease, transform 120ms ease;
}

.fancy-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.fancy-input:focus {
  outline: none;
  box-shadow: 0 6px 16px rgba(3, 8, 20, 0.6), 0 0 0 4px rgba(97, 218, 251, 0.08);
  transform: translateY(-2px);
}

.btn-add {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--gradient-blue);
  color: var(--text-dark);
  border: none;
  padding: 0.55rem 0.9rem;
  border-radius: var(--control-radius);
  font-weight: 600;
  cursor: pointer;
  transition: transform var(--ctrl-transition), box-shadow var(--ctrl-transition);
}

/* expense variant */
.btn-add.btn-expense {
  background: var(--gradient-orange);
  color: var(--text-orange-contrast);
}

/* Toggle is now just a color swatch (no knob/animation) */
.mode-toggle {
  z-index: 30;
  margin-left: 0.5rem;
  flex: 0 0 auto;
  width: 46px;
  height: 26px;
  padding: 3px;
  border-radius: 999px;
  border: none;
  background: rgba(255,255,255,0.06);
}

/* Mode-specific colors (using variables) with high specificity */
.panel.person-mode .mode-toggle,
.person-mode .mode-toggle {
  background: var(--gradient-blue);
}

.panel.expense-mode .mode-toggle,
.expense-mode .mode-toggle {
  background: var(--gradient-orange);
}

/* Respect reduced motion preference */
@media (prefers-reduced-motion: reduce) {
  .btn-add, .mode-toggle {
    transition: none !important;
  }
}

.names-list {
  text-align: left;
  padding-left: 1rem;
  margin: 0;
}

.name-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.5rem;
  padding: 0.18rem 0;
}

.name-text {
  flex: 1 1 auto;
}

/* botón eliminar */
.btn-delete {
  background: linear-gradient(90deg, #ff6b00 0%, #ffb347 100%);
  color: #371a00;
  border: none;
  padding: 0.25rem 0.45rem;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 700;
  line-height: 1;
}
.btn-delete:hover {
  filter: brightness(0.95);
}

.btn-clear {
  background: linear-gradient(90deg,#ff5a5a 0%,#ffb3b3 100%);
  color: #371a1a;
  padding: 0.45rem 0.8rem;
  border-radius: 8px;
  border: none;
}
.btn-clear:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.btn-clear:hover:enabled { filter: brightness(0.97); }

.fancy-select {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  background: linear-gradient(180deg, rgba(255,255,255,0.03), rgba(255,255,255,0.01));
  border: 1px solid rgba(255,255,255,0.06);
  padding: 0.55rem 0.75rem;
  border-radius: 10px;
  color: #fff;
  min-width: 160px;
  box-shadow: 0 4px 10px rgba(3,8,20,0.45);
}

/* small tweak so select and inputs align in .input-inline */
.input-inline .fancy-select,
.input-inline .fancy-input {
  min-width: 120px;
}

.expenses-list {
  list-style: none;
  margin: 0;
  padding-left: 1rem;
}

.expense-item {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  padding: 0.35rem 0;
  border-bottom: 1px solid rgba(255,255,255,0.02);
}

.expense-desc {
  font-weight: 600;
  color: #e6eefc;
  text-align: left;
}

.expense-meta {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  justify-content: space-between;
}

.expense-person {
  color: rgba(255,255,255,0.7);
  font-size: 0.9rem;
}

.expense-amount {
  font-weight: 700;
  color: #ffd9a8;
  min-width: 72px;
  text-align: right;
}

/* estilos para la lista de transacciones */
.transactions-list {
  list-style: none;
  margin: 0;
  padding-left: 1rem;
}

.transaction-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.35rem 0;
  border-bottom: 1px solid rgba(255,255,255,0.02);
}

.transaction-desc {
  color: #e6eefc;
  font-weight: 600;
}

.transaction-amount {
  color: #ffd9a8;
  font-weight: 700;
  min-width: 68px;
  text-align: right;
}

.transaction-conversion {
  font-size: 0.85rem;
  color: rgba(255,255,255,0.72);
  margin-top: 0.25rem;
  text-align: left;
}

.transaction-amount-block {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 0.2rem;
  min-width: 88px;
}

.transaction-amount-usd {
  font-weight: 700;
  color: #ffd9a8;
  text-align: right;
}

.transaction-amount-bs {
  font-size: 0.85rem;
  color: rgba(255,255,255,0.72);
  text-align: right;
}

.rate-info {
  font-size: 0.9rem;
  color: rgba(255,255,255,0.78);
  margin-top: 0.25rem;
}
