/* global box-sizing and font + custom properties */
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700&display=swap');

*, *::before, *::after {
  box-sizing: border-box;
}

:root {
  --gradient-blue: linear-gradient(90deg, #2b7cff 0%, #61dafb 100%);
  --gradient-orange: linear-gradient(90deg, #ff8a00 0%, #ffb347 100%);
  --text-dark: #071029;
  --text-orange-contrast: #371a00;
  --control-radius: 10px;
  --ctrl-transition: 120ms ease;
}

/* Apply global font */
body {
  margin: 0;
  font-family: 'Orbitron', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
